.mainContainer-contactForm {
    width: 100%;
    /* background: rgb(0, 0, 0); */
    position: relative;
    padding: 2rem;
}

.mainContainer-contactForm:before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.contactForm {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 1rem;
}

.contactForm a, p {
    color: black;
}

.contactForm h1 {
    margin-top: 1rem;
    color: white;
    font-size: 2rem;
}

.map .location-header {
    color: var(--primary-color);
}

.map {
    width: 300px;
    margin: auto;
}


.contact-container {
    width: 300px;
    margin: auto;
}

.contact-container h1 {
    padding-top: 2rem;
}

.contact-container form {
    height: 30rem;
    background-color: var(--primary-color);
    border-radius: 1rem;
}

.user-input {
    width: 16.5rem;
    height: 2rem;
    margin: 1rem;
    display: block;
    padding: 1rem;
    border: 2px solid var(--color-bg-variant);
    border-radius: 0.5rem;
}

.email-input {
    height: 10rem;
}

h1 {
    text-align: center;
}

.submit-btn button {
    color: black;
    background-color: rgb(255, 255, 255);
    text-align: center;
    margin-bottom: 1rem;
    text-align: center;
    margin-left: 6.5rem;
}

textarea {
    resize: none;
  }

/* ======================== MEDIA QUERIES ================================ */

@media screen and (max-width: 940px) {

    
    .map iframe{
        display: none;
    }

    .mainContainer-contactForm p {
        font-size: 14px;
    }

}

