.video-bg {
    width: 100%;
    height: 80vh;
    position: relative;
    top: 0;
    background: rgba(0, 0, 0, .1);
    object-fit: cover;
}


.video-bg::before {
    width: 100%;
    height: 80vh;
    position: absolute;
    top: 0;
    object-fit: cover;
    z-index: -1;
}

.hero .hero-container {
    top: 25%;
    position: absolute;
    align-items: center;
    text-align: center;
    width: 100%;
}

.hero-logo {
    margin: auto;
}

.subtitle {
    letter-spacing: .5rem;
}

