    background: url(/sacramento-rehab/static/media/menu-bg.5ca8f93b.jpg) no-repeat center center/cover;
    height: 70vh;
    width: 100%;
    z-index: -1;
}

.contact-hero .container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}

.contact-hero h1 {
    color: var(--primary-color);
    font-size: 5rem;
}

.contact-hero .content {
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}




